import React, { useContext } from 'react';
import { useTheme } from '@material-ui/core/styles';
import {
    BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, LabelList, Rectangle
  } from 'recharts';
import Title from './Title';
import { Appcontext } from '../AppContext';

const renderCustomizedLabel = (props) => {
  const {
    x, y, width, height, value,
  } = props;
  const radius = 10;

  return (
    <g>
      {/* <circle cx={x + width} cy={y + 2*radius} r={radius} fill="#fff" /> */}
      <text x={x + width + 20} y={y+ 2*radius} fill="#3f5776" textAnchor="middle" dominantBaseline="middle">
        {value}
      </text>
    </g>
  );
};

export default function StateChartH(){
  const theme = useTheme();
  const appContext = useContext(Appcontext)
  return (
    <React.Fragment>
      <Title>Today</Title>
      <BarChart
        width={1200}
        height={600}
        layout="vertical"
        data={appContext?.statescount}
        margin={{
          top: 5, right: 30, left: 20, bottom: 5,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" horizontal={false} />
        
        <XAxis
                        type="number"
                        dataKey="count"
                        axisLine={true}
                        stroke='#a0a0a0'
                        domain={[0, 'dataMax + 1000']}
                    />
                    <YAxis
                        type="category"
                        dataKey="state"
                        width={40}
                    />
        
        
        <Tooltip />
        <Legend />
        <Bar dataKey="count" fill="#8884d8" minPointSize={0}>
          <LabelList dataKey="count" content={renderCustomizedLabel} />
        </Bar>
      </BarChart>
    </React.Fragment>
  );
}