export class Constants {
   public getState(stateId) {
        const stateMap = {
            'ap': 'Andhra Pradesh',
            'an' : 'Arunachal Pradesh',
            'tg': 'Telangana',
            'mh' : 'Maharashtra',
            'dl' : 'Delhi',
            'gj' : 'Gujarat',
            'rj' : 'Rajasthan',
            'tn' : 'Tamilnadu',
            'up' : 'Uttar Pradesh'
        }

        return stateMap[stateId] ?? stateId;
    }
}