import React, { Fragment, Children } from 'react';
import { AppContextInterface, AppContextProvider } from '../AppContext';
import {Constants} from './../statemap'

interface DataProps {
    children: any
}

interface DataState {
  isLoaded: boolean; 
  items: any,
  error: {message : ''},
  totalcases: number,
  totalrecovered: number,
  totaldeceased: number,
  totalActive: number,
  statewise: any,
  statescount: any
};

export default class DataProvider extends React.Component<DataProps, DataState> {
    state: DataState = {
        isLoaded: false,
        items: [],
        error: {message : ''},
        totalcases: 0,
        statewise: [],
        statescount: [],
        totalrecovered: 0,
        totaldeceased: 0,
        totalActive: 0
    };

    constructor(props:DataProps) {
      super(props);
      this.state = {
        error: {message : ''},
        isLoaded: false,
        items: [],
        totalcases: 0,
        statewise: [],
        statescount:[],
        totalrecovered: 0,
        totaldeceased: 0,
        totalActive: 0
      };
    }
  
    componentDidMount() {
      fetch("https://api.covid19india.org/states_daily.json")
        .then(res => res.json())
        .then(
          (result) => {
            let statewise = new Array;
            let statesarray = new Array;
            let statesdata = result.states_daily;
            let countrysum = 0;
            let countryactive=0;
            let countryrecovered=0;
            let countrydeceased=0;
            for (let i =0; i<statesdata.length;i++){ 
              // For linear graph
              const date1: Date = new Date(statesdata[i]["date"]);
              const date2: Date = new Date();
              const diffTime: any = date2.valueOf() - date1.valueOf();;
              const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 

              if(date1 <= date2){
                if(statesdata[i]["status"] == 'Confirmed'){
                  countrysum = countrysum + parseInt(statesdata[i]['tt']);
                statesarray.push(
                    {'total': countrysum, 'date': statesdata[i]["date"]}
                  )
                }

                debugger;
                switch (statesdata[i]['status']){
                   case 'Recovered':
                      countryrecovered = countryrecovered + parseInt(statesdata[i]['tt']);
                      break;
                    case 'Deceased':
                      countrydeceased = countrydeceased + parseInt(statesdata[i]['tt']);
                      break;
                }
              }
             
              //for datewuise graph
              if(diffDays <= 10 && date1 <= date2){
                if(statesdata[i]["status"] == 'Confirmed'){
                  statewise.push({
                    "count": parseInt(statesdata[i]["tt"]), "date": statesdata[i]["date"]
                  })
                }
              }
            }

            let keysArray = Object.keys(statesdata[0]);

            //aggregate all the values for each row
            const statescount = statesdata.reduce(function(res, obj) {
              for (let i=0; i<keysArray.length; i++){
                const prop = keysArray[i];
                if(prop != 'date' && prop != 'tt' && prop != 'status'){
                  if(obj['status'] == 'Confirmed'){
                    if (res[prop] == undefined) res[prop]=0;
                    res[prop] += parseInt(obj[prop]);
                  }
                }
              }
              return res;
            }, {__array:[]});

            keysArray = Object.keys(statescount);
            let _statescount: any = []
            
            for (let i=0; i<keysArray.length; i++){
              if(statescount[keysArray[i]] > 700){
               _statescount.push({"state": new Constants().getState(keysArray[i]), 'count': statescount[keysArray[i]]})
              }
            }

            this.setState({
              isLoaded: true,
              items: statesarray,
              totalcases: countrysum,
              statewise: statewise,
              statescount: _statescount,
              totalActive: countrysum - (countryrecovered + countrydeceased),
              totaldeceased: countrydeceased,
              totalrecovered: countryrecovered
            });
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        )
    }
  
    render() {
      const { error, isLoaded, items, totalcases, statewise, statescount ,totalActive,totaldeceased, totalrecovered } = this.state;
      debugger;
      const chartAppContext: AppContextInterface = {
        data: items,
        total: totalcases,
        statewise: statewise,
        statescount: statescount,
        totalActive: totalActive,
        totaldeceased: totaldeceased,
        totalrecovered: totalrecovered
      };
      if (error.message != '') {
        return <div>Error: {error.message}</div>;
      } else if (!isLoaded) {
        return <div>Loading...</div>;
      } else {
        return (
            <AppContextProvider value={chartAppContext}>
                <Fragment>
                    {this.props.children}
                </Fragment>
            </AppContextProvider>
        );
      }
    }
  }